import React from "react";
import { TableComposable, Thead, Tr, Th, Tbody, Td, Button } from "@patternfly/react-table";
import LinkIcon from "@patternfly/react-icons/dist/esm/icons/external-link-alt-icon";
import { Card, CardBody, CardTitle, Icon, ClipboardCopy } from "@patternfly/react-core";

export default function List({ links }) {
  return (
    <Card>
      <CardTitle>All Links</CardTitle>
      <CardBody>
        <TableComposable aria-label="Simple table">
          <Thead>
            <Tr>
              <Th>Short</Th>
              <Th>Long URL</Th>
              <Td></Td>
            </Tr>
          </Thead>
          <Tbody>
            {links.map(link => (
              <Tr key={link._id}>
                <Td>
                  <ClipboardCopy variant="inline-compact" hoverTip="Copy" clickTip="Copied">
                    {link.ref}
                  </ClipboardCopy>
                </Td>
                <Td>
                  <span>{link.long_url}</span>
                </Td>
                <Td>
                  <a target="_blank" href={`./${link.ref}`}>
                    <Icon size="lg">
                      <LinkIcon />
                    </Icon>
                  </a>
                </Td>
              </Tr>
            ))}
          </Tbody>
        </TableComposable>
      </CardBody>
    </Card>
  );
}
