import React, { useState, Fragment } from "react";
import {
  Card,
  CardTitle,
  CardBody,
  CardFooter,
  InputGroup,
  TextInput,
  Button,
  Label,
  Form,
  FormGroup,
  ActionGroup,
  Alert,
} from "@patternfly/react-core";

export default function Shorten({ addLink }) {
  const [link, setLink] = useState("");
  const [isLoading, setLoading] = useState(false);
  const [state, setState] = useState("");

  async function handleSubmit(e) {
    e.preventDefault();
    if (isLoading) return;
    setLoading(true);
    const response = await fetch("/links", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ long_url: link }),
    });

    setLoading(false);

    if (response.status !== 201) return showError();
    else showSuccess();
    const data = await response.json();
    setLink("");
    addLink(data);
  }

  function showError() {
    setState("error");
    setTimeout(() => setState(""), 2000);
  }

  function showSuccess() {
    setState("success");
    setTimeout(() => setState(""), 2000);
  }

  return (
    <Fragment>
      {state === "success" && <Alert variant="success" title="Link added successfully" />}
      {state === "error" && <Alert variant="danger" title="Failed to add link" />}

      <Card>
        <CardTitle>Shorten a Link</CardTitle>
        <CardBody>
          <Form onSubmit={handleSubmit}>
            <FormGroup label="URL :">
              <TextInput type="text" value={link} onChange={setLink} isDisabled={isLoading} required />
            </FormGroup>
            <Button variant="primary" type="submit" disabled={isLoading}>
              Submit
            </Button>
          </Form>
        </CardBody>
      </Card>
    </Fragment>
  );
}
